import React, { useState, useEffect, useCallback } from "react";
import {
  Button,
  Text,
  FlatList,
  Center,
  useToast,
  Spinner,
  HStack,
  Pressable,
  VStack,
  Hidden,
} from "native-base";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

import Util from "../../../util";
import Colors from "../../../constants/Colors";
import * as signatureActions from "../../../store/actions/signatures";
import VerticalItem from "./VerticalItem";
// import NavListIcon from "../../../components/svg/NavListIcon";
// import AddIcon from "../../../components/svg/AddIcon";
// import AcreIcon from "../../../components/svg/AcreIcon";
import { getUser } from "../../../constants/Config";
import NavBar from "../home/NavBar";
import AddSignature from "./AddSignature";
import * as events from "../../../store/events";
import ShadowPagination from "../../../components/ShadowPagination";
import ShadowButton from "../../../components/ShadowButton";

const { vw, vh } = Util;

const PageHeader = ({
  listStyle,
  setListStyle,
  navigate,
  setIsOpen,
  dispatch,
}) => {
  return (
    <VStack
      style={{
        marginTop: vh(-10),
        marginHorizontal: vw(20),
        borderBottomColor: "rgba(0,0,0,0.15)",
        borderBottomWidth: 1,
      }}
    >
      <HStack justifyContent={"space-between"}>
        <Text
          style={{
            fontSize: vw(24),
            paddingTop: vh(30),
            width: vw(300),
            color: "#303030",
            fontFamily: "Poppins_medium",
          }}
        >
          {"Signatures"}
        </Text>
        <Hidden only={["sm", "base"]}>
          <HStack alignSelf={"center"}>
            <Pressable
              onPress={() => {
                navigate("/campaigns");
              }}
            >
              <Text
                style={{
                  fontSize: vw(15),
                  paddingTop: vh(40),
                  paddingHorizontal: 10,
                  color: "#303030",
                  fontFamily: "Poppins_medium",
                  marginHorizontal: vw(25),
                  paddingBottom: vh(13),
                  borderBottomWidth: vh(0),
                  borderBottomColor: "rgba(125, 43, 233, 1)",
                }}
              >
                Campaigns
              </Text>
            </Pressable>
            <Pressable
              onPress={() => {
                navigate("/templates");
              }}
            >
              <Text
                style={{
                  fontSize: vw(15),
                  paddingTop: vh(40),
                  paddingHorizontal: 10,
                  color: "#303030",
                  fontFamily: "Poppins_medium",
                  marginHorizontal: vw(25),
                  paddingBottom: vh(13),
                  borderBottomWidth: vh(0),
                  borderBottomColor: "rgba(125, 43, 233, 1)",
                }}
              >
                Templates
              </Text>
            </Pressable>
            <Pressable
              onPress={() => {
                navigate("/signatures");
              }}
            >
              <Text
                style={{
                  fontSize: vw(15),
                  paddingTop: vh(40),
                  paddingHorizontal: 10,
                  color: Colors.primary,
                  fontFamily: "Poppins_medium",
                  marginHorizontal: vw(25),
                  paddingBottom: vh(13),
                  borderBottomWidth: vh(4),
                  borderBottomColor: "rgba(125, 43, 233, 1)",
                }}
              >
                Signatures
              </Text>
            </Pressable>
          </HStack>
        </Hidden>
        <HStack
          style={{ marginTop: vh(20), width: vw(300) }}
          alignItems={"center"}
          justifyContent={"flex-end"}
          space={2}
        >
          <ShadowButton
            onPress={async () => {
              await dispatch({
                type: events.SET_CURRENT_SIGNATURE,
                data: null,
              });
              setIsOpen(true);
            }}
            label={"+ Create"}
            style={{ height: vh(36) }}
          />
        </HStack>
      </HStack>
      <Hidden from='md'>
        <HStack alignSelf={"center"}>
          <Pressable
            onPress={() => {
              navigate("/campaigns");
            }}
          >
            <Text
              style={{
                fontSize: vw(15),
                paddingTop: vh(20),
                paddingHorizontal: 10,
                color: "#303030",
                fontFamily: "Poppins_medium",
                marginHorizontal: vw(10),
                paddingBottom: vh(13),
                borderBottomWidth: vh(0),
                borderBottomColor: "rgba(125, 43, 233, 1)",
              }}
            >
              Campaigns
            </Text>
          </Pressable>
          <Pressable
            onPress={() => {
              navigate("/templates");
            }}
          >
            <Text
              style={{
                fontSize: vw(15),
                paddingTop: vh(20),
                paddingHorizontal: 10,
                color: "#303030",
                fontFamily: "Poppins_medium",
                marginHorizontal: vw(10),
                paddingBottom: vh(13),
                borderBottomWidth: vh(0),
                borderBottomColor: "rgba(125, 43, 233, 1)",
              }}
            >
              Templates
            </Text>
          </Pressable>
          <Pressable
            onPress={() => {
              navigate("/signatures");
            }}
          >
            <Text
              style={{
                fontSize: vw(15),
                paddingTop: vh(20),
                paddingHorizontal: 10,
                color: Colors.primary,
                fontFamily: "Poppins_medium",
                marginHorizontal: vw(10),
                paddingBottom: vh(13),
                borderBottomWidth: vh(4),
                borderBottomColor: "rgba(125, 43, 233, 1)",
              }}
            >
              Signatures
            </Text>
          </Pressable>
        </HStack>
      </Hidden>
    </VStack>
  );
};

const Index = ({ user, setLogout }) => {
  const navigate = useNavigate();
  const toast = useToast();
  const dispatch = useDispatch();
  const [limit, setLimit] = useState(50);
  const [page, setPage] = useState(1);
  const [isOpen, setIsOpen] = React.useState(false);
  const [listStyle, setListStyle] = useState("vertical");
  const [isLoading, setIsLoading] = useState(false);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const { userSignatures: signatures, total } = useSelector(
    (state) => state.signatures
  );
  const profile = getUser();

  const _showAlert = useCallback(
    (status, msg) => {
      toast.show({
        placement: "top",
        render: () => {
          let bg = "red.300";
          if (status === "error") {
            bg = "red.300";
          } else if (status === "success") {
            bg = "green.300";
          } else if (status === "info") {
            bg = "blueGray.300";
          } else if (status === "warning") {
            bg = "orange.300";
          }
          return (
            <Text
              style={{
                fontSize: vw(12),
                fontFamily: "Poppins_medium",
                color: "#33214B",
                zIndex: 9999999,
              }}
              p={2}
              bg={bg}
              rounded={"full"}
            >
              {msg}
            </Text>
          );
        },
      });
    },
    [toast]
  );

  const loadSignatures = useCallback(async () => {
    setIsRefreshing(true);
    try {
      await dispatch(signatureActions.fetchSignatures(1, 50));
    } catch (err) {
      _showAlert("error", err.message);
    }
    setIsRefreshing(false);
  }, [dispatch, _showAlert]);

  useEffect(() => {
    setIsLoading(true);
    loadSignatures().then(() => {
      setIsLoading(false);
    });
    return () => {
      setIsLoading(false);
      setIsRefreshing(false);
    };
  }, [dispatch, loadSignatures]);

  const NoItemsFound = () => {
    return (
      <Center mt={250} flex={1}>
        <Text>No items found.</Text>
      </Center>
    );
  };

  const cardwidth = 250;
  let cols = Math.floor(window.screen.width / cardwidth);
  if (cols === 0) {
    cols = 1;
  }

  const ph = 100 - parseFloat(vh(70)) + "vh";

  return (
    <VStack minHeight={"100vh"} bg={"white"} flex={1}>
      <NavBar user={user} setLogout={setLogout} />
      <VStack height={ph}>
        <PageHeader
          navigate={navigate}
          setListStyle={setListStyle}
          listStyle={listStyle}
          setIsOpen={setIsOpen}
          dispatch={dispatch}
        />
        {isLoading ? (
          <Center flex={1}>
            <Spinner size={"small"} color={Colors.primary} />
          </Center>
        ) : (
          <>
            {cols === 1 ? (
              <FlatList
                key={"list-" + cols}
                contentContainerStyle={{ paddingBottom: 20 }}
                ListEmptyComponent={<NoItemsFound />}
                refreshing={isRefreshing}
                removeClippedSubviews={true}
                data={signatures.filter((f) => f.createdby === profile._id)}
                keyExtractor={(item) => item._id}
                renderItem={({ item }) => {
                  return (
                    <VerticalItem
                      cols={cols}
                      item={item}
                      setIsOpen={setIsOpen}
                    />
                  );
                }}
              />
            ) : (
              <FlatList
                key={"list-" + cols}
                contentContainerStyle={{ paddingBottom: 20 }}
                ListEmptyComponent={<NoItemsFound />}
                refreshing={isRefreshing}
                removeClippedSubviews={true}
                data={signatures.filter((f) => f.createdby === profile._id)}
                keyExtractor={(item) => item._id}
                renderItem={({ item }) => {
                  return (
                    <VerticalItem
                      cols={cols}
                      item={item}
                      setIsOpen={setIsOpen}
                    />
                  );
                }}
                columnWrapperStyle={{
                  justifyContent: "space-between",
                }}
                numColumns={5}
              />
            )}
            {total > limit && (
              <ShadowPagination
                limit={limit}
                total={total}
                page={page}
                setPage={setPage}
                setIsLoading={setIsLoading}
                setLimit={setLimit}
              />
            )}
          </>
        )}
        {isOpen && (
          <AddSignature
            isOpen={isOpen}
            setIsOpen={setIsOpen}
            _showAlert={_showAlert}
          />
        )}
      </VStack>
    </VStack>
  );
};

export default Index;
