import React from "react";
import { VStack, Text, Image, Center, Pressable } from "native-base";
import { useDispatch } from "react-redux";

import Util from "../../../util";
import UserIcon from "../../../components/svg/UserIcon";
import Colors from "../../../constants/Colors";
import { CDN } from "../../../constants/Config";
import * as events from "../../../store/events";

const { vw, vh } = Util;

const VerticalItem = ({ item, setIsOpen, cols }) => {
  const dispatch = useDispatch();

  return (
    <Pressable
      onPress={async () => {
        await dispatch({ type: events.SET_CURRENT_SIGNATURE, data: item });
        setIsOpen(true);
      }}
    >
      <VStack
        style={{
          boxShadow: "0px 3px 10px #0000001A",
          width: cols === 1 ? "88%" : vw(250),
          height: vh(270),
          marginHorizontal: vw(20),
          marginVertical: vh(20),
          borderRadius: vw(10),
        }}
      >
        <Center
          style={{
            backgroundColor: "#F2F4F8",
            width: cols === 1 ? "100%" : vw(250),
            height: vh(200),
            borderRadius: vw(10),
          }}
        >
          {item.image === "0" ? (
            <UserIcon width={96} height={96} />
          ) : (
            <Image
              style={{
                borderRadius: vw(10),
              }}
              alt='Pic'
              onLoadEnd={() => {}}
              width={vw(250)}
              height={vh(200)}
              resizeMode='contain'
              source={{
                uri: `${CDN}/signatures/${item.image}`,
              }}
            />
          )}
        </Center>

        <Text
          style={{
            marginTop: 10,
            color: Colors.primary,
            fontFamily: "Poppins_medium",
            fontSize: vw(15),
            marginLeft: vw(20),
          }}
        >
          {item.name}
        </Text>

        <Text
          style={{
            marginTop: 6,
            color: "#686868",
            fontFamily: "Poppins_medium",
            fontSize: vw(13),
            marginLeft: vw(20),
          }}
        >
          {item.label}
        </Text>
      </VStack>
    </Pressable>
  );
};

export default VerticalItem;
